* {
  box-sizing: border-box;
}

html {
  font-size: 10px;
}

body {
  background: #6C7A89;
  min-height: 100vh;
  display: flex;
  font-size: 16px;
  font-weight: 400;
  font-family: Roboto, Arial, Helvetica, sans-serif;
  overflow: hidden;
}

h1,h2,h3,h4,h5,h6,label,span {
  font-size: 1.4rem;
  font-family: Ubuntu;
}

#root {
  min-height: 100vh;
}

.btn, .form-control, .custom-select {
  font-size: 1.4rem;
}

.App, #root, .auth-wrapper {
  width: 100%;
  height: 100%;
}

.navbar-light {
  background-color: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
}

.auth-wrapper {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
  vertical-align: middle;
  height: 100vh;
}

.auth-inner {
  width: 450px;
  margin: auto;
  background: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 40px 55px 45px 55px;
  border-radius: 15px;
  transition: all .3s;
}

.auth-wrapper .form-control:focus {
  border-color: #167bff;
  box-shadow: none;
}

.auth-wrapper h3 {
  text-align: center;
  margin: 0;
  line-height: 1;
  padding-bottom: 20px;
}

.custom-control-label {
  font-weight: 400;
}

.forgot-password,
.forgot-password a {
  text-align: right;
  font-size: 13px;
  padding-top: 10px;
  color: #7f7d7d;
  margin: 0;
}

.forgot-password a {
  color: #167bff;
}

table {
  display: flex;
  flex-flow: column;
  width: 100%;
}

thead {
  flex: 0 0 auto;
  width: 98%;
  font-size: 14px;
  height: 5vh;
  margin-bottom: 2px;
}

tbody {
  flex: 1 1 auto;
  max-height: 64vh;
  overflow-y: hidden;
  padding-bottom: 5px;
}

tr {
  width: 100%;
  display: table;
  table-layout: fixed;
}